import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../../../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;
import { ToastComponent } from '../../../../../../layouts/toast-notification/toast.component';


@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./follow-up-description-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class FollowUpDescriptionAddComponent    {
    @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	
	 followupdesc= new FollowUpDescriptionData();  
	 followupdescForm: FormGroup;
	 followupdescId = 0;  
	 status;
	 bootstrapFormValues = [];
	 msg = [];
 


 constructor(private http: HttpClient, private router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.followupdescId).subscribe(params=>{
			if(params.followupdescId != undefined){
				this.followupdescId=params.followupdescId; 
				}
		}); 
	
	if(this.followupdescId == 0){
		this.status="Add";
	}else{
		this.status="Update";
		this.populateData();
	}
	}
	
     ngOnInit() {
		this.followupdescForm = new FormGroup({			
			'followupdescId' : new FormControl(''),
			'followupdescName' : new FormControl('', [ Validators.required]),
			'followupdescEntryId' : new FormControl(''),
			'followupdescEntryDate' : new FormControl(''),
			'followupdescModifiedId' : new FormControl(''),
			'followupdescModifiedDate' : new FormControl(''),
		});
  }
	  
	    get followupdescName(){ return this.followupdescForm.get('followupdescName')};
	    get followupdescEntryId(){ return this.followupdescForm.get('followupdescEntryId')};
	    get followupdescEntryDate(){ return this.followupdescForm.get('followupdescEntryDate')};
	    get followupdescModifiedId(){ return this.followupdescForm.get('followupdescModifiedId')};
	    get followupdescModifiedDate(){ return this.followupdescForm.get('followupdescModifiedDate')};
	 
	  
	  populateData(){
		const headers = new HttpHeaders({'Content-Type':'application/json'});
		
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/followupdesc-data',
									JSON.stringify({ followupdescId :this.followupdescId,
												   	requestType : "update" }),
						{ headers, withCredentials: true }).subscribe(
			 										data => {
			 													this.followupdesc = data['populateData'][0];
															},
													(err) => {
																console.log("ERROR: "+err);
															 });			 
		}
	   
	 
	  
	  ngAfterViewInit(){
		    $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
	  }
 
 	 toggleAnimation(){
			this.router.navigate(['portal/follow-up-description-list']);
		 }
		   

	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.followupdesc[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
			}
		
		 if(!this.followupdescForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.followupdescId == 0){ 
			this.followupdesc['requestType'] = 'add';
		}else{
			this.followupdesc['requestType'] = 'update';
			this.followupdesc['followupdescId'] = this.followupdescId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/followupdesc',
									JSON.stringify(this.followupdesc),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.followupdescForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
			
			
			}
	  }


  onDelete(){
		 this.followupdesc['requestType'] = 'delete';
		 this.followupdesc['followupdescId'] = this.followupdescId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/followupdesc',
									JSON.stringify(this.followupdesc),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.followupdescForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
		 
	 }
}

export class FollowUpDescriptionData {
	
	followupdescId: string ;
	followupdescName: string;
	followupdescEntryId:string;
	followupdescEntryBy:string;
	followupdescEntryDate:string;
    followupdescModifiedId:string;
    followupdescModifiedBy:string;
    followupdescModifiedDate:string;
	
	 
}



